import React from "react";
import { Container, Row, Col } from "reactstrap";
import profile from "../profile";
import { MDBMedia } from 'mdbreact';
import { Link } from 'react-router-dom';

class Experience extends React.Component {
  render() {
    return <Container>
      <Row>
        <Col>
          {profile.projects.map(function (projects, i) {

            return (
              <div key={i}>
                <MDBMedia >
                  {/* <MDBMedia left top href={projects.url}> */}
                  {/* <Link key={projects.id} to={`/${projects.id}`}></Link> */}
                    {/* <MDBMedia left top object src={projects.logo} alt=''/> */}
                  </MDBMedia>
                  <MDBMedia body>
                    {projects.concept.map(function (concept, i) {
                      return <div key={i}>
                        <Link className="links" key={projects.id} to={`/${projects.id}`}><h5>{concept.title}</h5></Link>
                        <p className="jobDescription">{concept.description}</p>
                      </div>
                    })}
                  {/* </MDBMedia> */}

                </MDBMedia>

              </div>
            );
          })}
        </Col>
      </Row>
    </Container>
  }
}

export default Experience;